
import { MENUS, Menu } from './menus';
import { injectAppNavigator } from '../navigator/app-navigator';
export default {
  name: 'app-menu',
  setup() {
    const navigator = injectAppNavigator();
    return {
      menus: MENUS,
      navigator,
      handleClickMenu(menu: Menu) {
        navigator.methods.go(menu.page);
      },
    };
  },
};
